.footer__author {
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: calc(17/14*100%);
    color: #545454;
}

@media screen and (min-width: 1024px) {
    .footer__author {
        font-size: 18px;
        line-height: calc(22/18*100%);
    }
} 
